.desktopAppliancePageContainer {
    display: none;
}

.mobileAppliancePageContainer {
    background-color: var(--color-gray-07);
}

/* HEADER  */
.gridContainer1 {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-row: 1fr;
    padding: 4rem 0;
}

.gridWrapper1 {
    grid-column: 1/-1;

    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-row: 1fr;
}

.gridItem1 {
    /* grid-column: 2/12; */
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.titleText {
    color: var(--color-gray-03);
}

.subtitleText {
    color: var(--color-gray-01);
}


.buttonWrapper button {
    width: 100%;
}

.colorButtonWrapper {
    display: flex;
    flex-direction: column;
    gap: 4rem;
}

/* ---------------------------------------------------- */
/* MOBILE HEADER */
/* ---------------------------------------------------- */
.mobileHeaderWrapper {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 1rem;
}

.mobileProductImageWrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    height: auto;
    justify-items: center;
    align-items: center;
}

.mobileProductImageShape {
    grid-row: 1;
    grid-column: 1;
    width: auto;
    height: auto;
    width: 30rem;
}

.mobileProductImageShape svg {
    height: 100%;
    width: 100%;
    display: flex;
}

.mobileProductImage {
    grid-row: 1;
    grid-column: 1;
    height: 30rem;
    max-width: 12rem;
    display: flex;
}

.mobileProductImage img {
    height: 100%;
    width: 100%;
    object-fit: contain;
    display: flex;
}


.sectionTextWrapper {
    padding-bottom: 2rem;
}

.applianceHeaderWrapperDiv1BtnWrapper button {
    width: max-content;
}

@media (min-width: 430px) {

    /* ---------------------------------------------------- */
    /* MOBILE HEADER */
    /* ---------------------------------------------------- */
    .mobileProductImageShape {
        width: 40rem;
    }

    .mobileProductImage {
        height: 40rem;
        max-width: 20rem;
    }
}


@media (min-width: 768px) {

    /* ---------------------------------------------------- */
    /* MOBILE HEADER */
    /* ---------------------------------------------------- */
    .mobileProductImageShape {
        width: 50rem;
    }

    .mobileProductImage {
        height: 50rem;
        max-width: 30rem;
    }

}

