.buttonsWrapper {
    padding: 2rem 0 0 0;
    display: flex;
    flex-direction: row;
    gap: 1rem;
}
.buttonsWrapper button{
    width: 100%;
}

@media print {
    .overlayContainer{
        display: none;
    }
}
@keyframes gradientAnimation {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

